<template>
	<div class="create_one mt-5 position-relative">
		<b-container>
			<div class="header mt-4 d-flex align-items-center justify-content-between">
				<div class="h4 mb-0 font-weight-normal">{{ $t("createGroupInvite") }}</div>
				<button class="fs-sm mx-3 filter_btn active" @click="preview">
					<BIcon icon="eye" scale="1.5" class="mr-2" />
					{{ $t("previewInvite") }}
				</button>
			</div>
			<div class="field_title mt-4 pt-1">
				<div class="title">{{ $t("availableTimes") }}</div>
			</div>
			<b-form class="mt-4" @submit.prevent="next">
				<b-row>
					<!-- Invite duration -->
					<b-col cols="12" lg="8" class="mb-4">
						<form-select :label="$t('eventDuration')" :value="invite.duration" :options="durationOptions" @change="durationChanged" required v-visible="'duration'">
							<BIcon slot="icon_left" icon="clock-history" />
							<virtual-input v-if="durationPlaceholder" @click="$bvModal.show('DurationModal')" class="mb-0">
								<span slot="placeholder" class="text-black">{{ durationPlaceholder }}</span>
								<BIcon slot="icon_right" icon="pencil" />
							</virtual-input>
						</form-select>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 25px" data-visible="duration">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
							<p class="ml-3 mb-0">{{ $t("durationDesc") }}</p>
						</div>
					</b-col>

					<!-- Invite slotIncrement -->
					<b-col cols="12" lg="8" class="mb-4">
						<form-select :label="$t('timeSlotIncrement')" v-model="invite.slotIncrement" :options="slotIncrementOptions" required v-visible="'slotIncrement'">
							<BIcon slot="icon_left" icon="clock" />
						</form-select>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 25px" data-visible="slotIncrement">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
							<p class="ml-3 mb-0">
								{{ $t("slotIncrementDesc") }}
							</p>
						</div>
					</b-col>

					<!-- Invite dateRange -->
					<b-col cols="12" lg="8" class="mb-4">
						<form-select :label="$t('dateRange')" :value="dayRangeValue" :options="dateRangeOptions" @change="dayRangeChanged" required v-visible="'dateRange'">
							<BIcon slot="icon_left" icon="calendar3" />
							<virtual-input v-if="!!dateRangeStr" @click="$bvModal.show('DateRangeModal')" class="mb-0" :inputProps="{ class: dateRangeStr ? 'd-inline-block w-auto' : '' }">
								<span slot="placeholder" class="text-black">{{ dateRangeStr }}</span>
								<BIcon slot="icon_right" icon="pencil" />
							</virtual-input>
						</form-select>
					</b-col>
					<b-col cols="4" class="d-none d-lg-block">
						<div class="d-flex position-absolute fs-sm text-muted" style="top: 25px" data-visible="dateRange">
							<BIcon icon="info-circle" scale="1.5" class="mt-1" />
							<p class="ml-3 mb-0">
								{{ $t("dateRangeDesc") }}
							</p>
						</div>
					</b-col>

					<!-- Select Availability -->
					<b-col cols="12">
						<div class="availability mt-4">
							<div class="availability_header d-flex w-100 align-items-center justify-content-between flex-wrap">
								<div class="text-muted font-weight-500">
									<span>{{ $t("selectAvailability") }}</span>
									<span v-b-tooltip.hover :title="$t('selectAvailabilityTooltip')">
										<BIcon icon="question-circle" scale="1.2" class="mx-2" style="opacity: 0.5" />
									</span>
								</div>
								<div
									class="py-2 px-3 rounded-pill font-weight-500 d-flex align-items-center justify-content-center"
									:style="`${invite.preventConflict ? 'background-color: #f8e71c1c; color: #948a19' : 'background-color: #ff001a1a; color: #0000008a'}`"
								>
									<span v-b-tooltip.hover :title="$t('doubleBookingPreventionToolTip')">
										<BIcon icon="info-circle" scale="1.2" />
									</span>
									<span class="fs-sm mx-2">{{ $t("doubleBookingPrevention") }}</span>
									<b-checkbox switch v-model="invite.preventConflict"></b-checkbox>
								</div>
							</div>
							<div class="availability_body my-4">
								<div class="d-flex align-items-center justify-content-between flex-wrap mb-3">
									<div class="availability_btns_controls d-flex align-items-center justify-content-between fs-sm font-weight-600 flex-wrap" style="height: 35px">
										<div class="before_btn d-flex align-items-center" @click="prevDateRange" :class="{ disabled: isPrevBtnDisabled }">
											<BIcon icon="chevron-right" v-if="$isRtl" />
											<BIcon icon="chevron-left" v-else />
											<span class="mx-1">
												{{ $t("before") }}
												<span class="d-none d-sm-inline-block">{{ FDOL.format("MMM D") }}</span>
											</span>
										</div>
										<div class="after_btn d-flex align-items-center ml-4" @click="nextDateRange" :class="{ disabled: isNextBtnDisabled }">
											<span class="mx-1">
												{{ $t("after") }}
												<span class="d-none d-sm-inline-block">{{ EDOL.format("MMM D") }}</span>
											</span>
											<BIcon icon="chevron-left" v-if="$isRtl" />
											<BIcon icon="chevron-right" v-else />
										</div>
										<div
											class="extend_btn d-flex align-items-center ml-4"
											v-if="isNextBtnDisabled"
											@click="extendDateRange"
											v-b-tooltip.hover
											:title="$t('extendTooltip')"
										>
											<BIcon icon="plus-lg" />
											<span class="mx-1"
												>{{ $t("extend") }} <span class="d-none d-sm-inline-block">{{ $t("dateRange") }}</span></span
											>
										</div>
									</div>
									<div class="availablity_timezone_control d-flex align-items-center">
										<time-zones v-model="invite.timezone"></time-zones>
										<span class="btn_icon ml-2">
											<NotAllowIcon width="1.5rem" style="fill: #5e5e5e" />
										</span>
									</div>
								</div>
								<b-list-group>
									<AvailableItem v-for="(date, i) in range" :key="i" :date="date" :availableDates="availableDates" @click="openAvailabilityModal" />
								</b-list-group>
							</div>
						</div>
					</b-col>
				</b-row>
			</b-form>
		</b-container>
		<div class="footer_control bg-white border-top w-100">
			<b-container class="h-100">
				<div class="d-flex justify-content-end align-items-center h-100">
					<b-button class="filter_btn active mr-3 font-weight-500" size="sm" @click="back">
						<BIcon :icon="`${!$isRtl ? 'chevron-left' : 'chevron-right'}`" scale="0.8" class="mr-2" />
						{{ $t("back") }}
					</b-button>
					<b-button variant="primary" @click="next" class="px-3"> {{ $t("next") }} </b-button>
				</div>
			</b-container>
		</div>
		<DurationModal v-model="invite.duration" />
		<DateRangeModal :value="invite.dateRange" @submit="dateRangeChanged" />
		<AvailabilityModal :duration="invite.duration" ref="availabilityModal" @submit="availableDateChanged" />
		<PreviewInvite ref="preview" />
	</div>
</template>

<script>
	import FormInput from "@/components/inputs/FormInput";
	import FormSelect from "@/components/inputs/FormSelect";
	import FormTextArea from "@/components/inputs/FormTextArea";
	import VirtualInput from "@/components/inputs/VirtualInput";
	import TimeZones from "@/components/TimeZones";
	import CameraPlus from "@/components/icons/cameraPlus";
	import ZoomIcon from "@/components/icons/zoom";
	import GoogleMeetIcon from "@/components/icons/googleMeet";
	import URLModal from "@/components/modals/URLModal";
	import DurationModal from "@/components/modals/DurationModal";
	import DateRangeModal from "@/components/modals/DateRangeModal";
	import AvailabilityModal from "@/components/modals/AvailabilityModal";
	import NotAllowIcon from "@/components/icons/notAllow.vue";
	import PreviewInvite from "@/components/PreviewInvite";

	import { RRule, RRuleSet, rrulestr } from "rrule";

	import { validationMixin } from "vuelidate";

	import { required } from "vuelidate/lib/validators";

	import AvailableItem from "@/components/AvailableItem.vue";

	import store from "@/store";

	export default {
		components: {
			FormInput,
			FormTextArea,
			VirtualInput,
			CameraPlus,
			ZoomIcon,
			GoogleMeetIcon,
			URLModal,
			DurationModal,
			DateRangeModal,
			FormSelect,
			AvailabilityModal,
			TimeZones,
			NotAllowIcon,
			AvailableItem,
			PreviewInvite,
		},

		mixins: [validationMixin],

		data() {
			return {
				invite: {
					duration: 30,
					slotIncrement: 30,
					dateRange: { type: "days", start: this.$moment().utc().startOf("date"), end: this.$moment().utc().add(6, "d").startOf("date") },
					timezone: "",
					preventConflict: true,
					availability: [],
				},
				firstDateOfList: this.$moment().utc().startOf("date"),
			};
		},

		validations: {
			invite: {
				duration: { required },
				slotIncrement: {},
				dateRange: {},
				timezone: {},
				preventConflict: {},
				availability: {},
			},
		},

		watch: {
			"invite.dateRange.start"(val) {
				this.firstDateOfList = val;
			},
			"invite.dateRange": {
				handler(newValue, oldValue) {
					let { start, end, type } = newValue;

					let availability = [];

					for (let available of this.invite.availability) {
						if (type == "indefinite") {
							available.rrule = available.rrule.replace(/(;UNTIL=.+Z)|(UNTIL=.+Z)/g, "");

							availability.push(available);
							continue;
						}

						let rruleSet = rrulestr(available.rrule);

						if (rruleSet.toString().indexOf("RDATE") != -1) {
							let singleDate = rruleSet.all()[0];

							singleDate = this.$moment(singleDate).utc().startOf("date");

							if (singleDate.isBefore(start, "date") || singleDate.isAfter(end, "date")) continue;

							availability.push(available);
							continue;
						}

						let startRegex = /DTSTART:(.+)Z\\/g;

						let untilRegex = /UNTIL=(.+)Z/g;

						available.rrule = available.rrule.replace(startRegex, `DTSTART:${start.locale("en").format("YYYYMMDDTHHmmss")}Z\\`);

						if (oldValue.type == "indefinite") {
							available.rrule = available.rrule + `;UNTIL=${end.locale("en").format("YYYYMMDDTHHmmss")}Z`;
						} else {
							available.rrule = available.rrule.replace(untilRegex, `UNTIL=${end.locale("en").format("YYYYMMDDTHHmmss")}Z`);
						}

						availability.push(available);
					}

					this.invite.availability = availability;
				},
				deep: true,
			},
		},

		async beforeRouteEnter(to, from, next) {
			let invite = store.state.invites.one;

			if (!invite.name) return next("/create/group");

			next((vm) => vm.init(invite));
		},

		computed: {
			durationOptions() {
				return this.$store.state.invites.durationOptions;
			},

			durationPlaceholder() {
				let isCustom = !this.durationOptions.some((d) => d.value == this.invite.duration);

				if (!isCustom) return "";

				let minutes = this.invite.duration;

				if (minutes < 60) return this.$moment.duration(minutes, "minutes").humanize();

				let hours = Math.floor(minutes / 60);

				minutes = minutes % 60;

				if (minutes === 0) return this.$moment.duration(hours, "hours").humanize();

				hours = this.$moment.duration(hours, "hours").humanize();

				minutes = this.$moment.duration(minutes, "minutes").humanize();

				let separator = this.$isRtl ? " Ùˆ " : " ";

				return hours + separator + minutes;
			},

			slotIncrementOptions() {
				return this.$store.state.invites.slotIncrementOptions;
			},

			dateRangeOptions() {
				return this.$store.state.invites.dateRangeOptions;
			},

			dateRangeStr() {
				let { type, start, end } = this.invite.dateRange;

				if (type == "indefinite") return this.$t("indefinite");

				if (type == "days") {
					let days = end.clone().diff(start, "days");

					if ([3, 7, 31].includes(days)) return "";

					return this.$moment.duration(days, "days").humanize() + " " + this.$t("intoFuture");
				}

				if (type == "specific") {
					const isSameYear = (date) => date.year() === this.$moment().year();

					start = start.format(`MMM D${isSameYear(start) ? "" : ", YYYY"}`);

					end = end
						.clone()
						.add(-1, "d")
						.format(`MMM D${isSameYear(end) ? "" : ", YYYY"}`);

					if (start === end) return start;

					return `${start} - ${end}`;
				}

				return "";
			},

			// First Day of List
			FDOL() {
				let { start } = this.invite.dateRange;

				return start.isSameOrAfter(this.firstDateOfList) ? start.clone() : this.firstDateOfList;
			},

			// End Day of List
			EDOL() {
				let { end, type } = this.invite.dateRange;

				let EDOL = this.firstDateOfList.clone().add(7, "d");

				if (type == "indefinite") return EDOL.clone().add(-1, "d");

				EDOL = end.isSameOrBefore(EDOL) ? end.clone() : EDOL;

				return EDOL.clone().add(-1, "d");
			},

			until() {
				if (this.invite.dateRange.type == "indefinite") return null;

				return this.invite.dateRange.end.clone();
			},

			range() {
				if (!this.FDOL.isValid() || !this.EDOL.isValid()) return [];

				let start = this.FDOL.clone();

				let end = this.EDOL.clone();

				let range = [];

				while (start.isSameOrBefore(end)) {
					range.push(start.clone());
					start.add(1, "d");
				}

				return range;
			},

			availableDates() {
				return this.invite.availability.map((av) => {
					let rruleSet = rrulestr(av.rrule);

					return {
						duration: av.duration,
						dates: rruleSet.between(this.FDOL.toDate(), this.EDOL.clone().add(1, "d").toDate()),
						rruleSet,
					};
				});
			},

			dayRangeValue: {
				get() {
					let { start, end, type } = this.invite.dateRange;

					if (type == "indefinite") return 6;

					return end.clone().diff(start, "days");
				},
				set(days) {
					let start = this.$moment().utc().startOf("date");

					let end = this.$moment().utc().add(days, "d").startOf("date");

					this.invite.dateRange = { type: "days", start, end };
				},
			},

			isNextBtnDisabled() {
				let { end, type } = this.invite.dateRange;

				if (type == "indefinite") return false;

				let EDOL = this.EDOL.clone().add(1, "d");

				return end.isBetween(this.FDOL, EDOL) || end.isSame(EDOL, "d");
			},

			isPrevBtnDisabled() {
				let { start } = this.invite.dateRange;

				return start.isSame(this.FDOL, "d");
			},
		},

		methods: {
			init(invite) {
				for (let key in invite) {
					this.invite[key] = invite[key];
				}

				this.firstDateOfList = this.invite.dateRange.start.clone();
			},

			durationChanged(value) {
				if (value == "custom") return this.$bvModal.show("DurationModal");

				this.invite.duration = value;
			},

			dayRangeChanged(value) {
				if (value == "custom") return this.$bvModal.show("DateRangeModal");

				this.dayRangeValue = value;
			},

			dateRangeChanged(value) {
				this.invite.dateRange = value;
			},

			nextDateRange() {
				let { end, type } = this.invite.dateRange;

				let FDOL = this.firstDateOfList.clone().add(7, "d");

				if (type != "indefinite" && end.isSameOrBefore(FDOL)) {
					FDOL = end.clone();
				}

				this.firstDateOfList = FDOL;
			},

			prevDateRange() {
				let { start } = this.invite.dateRange;

				let FDOL = this.firstDateOfList.clone().subtract(7, "d");

				this.firstDateOfList = start.isSameOrAfter(FDOL) ? start.clone() : FDOL;
			},

			extendDateRange() {
				this.invite.dateRange.end = this.invite.dateRange.end.clone().add(7, "d");
			},

			openAvailabilityModal(available) {
				this.$refs.availabilityModal.show(available);
			},

			hourAndMinute(number) {
				let hour = Math.floor(number);

				let minute = Math.round((number - hour) * 60);

				return { hour, minute };
			},

			availableDateChanged(value) {
				let availability = [];

				let isAvailableExist = (available) => !!availability.find((av) => av.duration == available.duration && av.rrule == available.rrule);

				let { date, intervals, repeats } = value;

				if (this.invite.availability.length == 0) {
					if (repeats == "only") {
						for (let interval of intervals) {
							let { start, end } = interval;

							let minutes = (end - start) * 60;

							let { hour, minute } = this.hourAndMinute(start);

							let rdate = "RDATE:" + date.clone().hour(hour).minute(minute).locale("en").format("YYYYMMDDTHHmmss") + "Z";

							let av = { duration: minutes, rrule: rdate };

							if (!isAvailableExist(av)) availability.push(av);
						}
					} else if (repeats == "all") {
						for (let interval of intervals) {
							let { start, end } = interval;

							let minutes = (end - start) * 60;

							let { hour, minute } = this.hourAndMinute(start);

							let rruleSet = new RRuleSet();

							rruleSet.rrule(
								new RRule({
									freq: RRule.WEEKLY,
									dtstart: this.invite.dateRange.start.clone().toDate(),
									until: this.invite.dateRange.end.clone().toDate(),
									byweekday: [date.clone().weekday()],
									byhour: [hour],
									byminute: [minute],
								})
							);

							let av = { duration: minutes, rrule: rruleSet.toString() };

							if (!isAvailableExist(av)) availability.push(av);
						}
					} else if (repeats == "weekdays") {
						for (let interval of intervals) {
							let { start, end } = interval;

							let minutes = (end - start) * 60;

							let { hour, minute } = this.hourAndMinute(start);

							let rruleSet = new RRuleSet();

							rruleSet.rrule(
								new RRule({
									freq: RRule.WEEKLY,
									dtstart: this.invite.dateRange.start.clone().toDate(),
									until: this.invite.dateRange.end.clone().toDate(),
									byweekday: [RRule.MO, RRule.TU, RRule.WE, RRule.TH, RRule.FR],
									byhour: [hour],
									byminute: [minute],
								})
							);

							let av = { duration: minutes, rrule: rruleSet.toString() };

							if (!isAvailableExist(av)) availability.push(av);
						}
					}

					this.invite.availability = availability;

					return;
				}

				let isValueAdded = false;

				if (intervals.length == 0) {
					isValueAdded = true;
				}

				let weekdayStr = date.clone().locale("en").format("dd").toUpperCase();

				for (let available of this.invite.availability) {
					let { duration, rrule } = available;

					/*
						weekdays: MO, TU, WE, TH, FR
						DTSTART:20220814T000000Z\nRRULE:FREQ=WEEKLY;BYDAY=TU,WE,TH,FR;BYHOUR=9;BYMINUTE=0;UNTIL=20220821T000000Z

						all: one of [MO, TU, WE, TH, FR, SA, SU]
						DTSTART:20220814T000000Z\nRRULE:FREQ=WEEKLY;UNTIL=20220821T000000Z;BYDAY=MO;BYHOUR=6;BYMINUTE=0

						only day
						RDATE:20220815T040000Z
					*/

					let rruleType = "",
						weekdays = this.weekdaysFromRRuleString(rrule),
						hour,
						minute,
						rruleHasExclude = rrule.indexOf("EXDATE") != -1,
						dateExStr = "",
						exDates = [];

					if (weekdays.length == 0) {
						rruleType = "only";
					} else {
						rruleType = weekdays.length == 1 ? "all" : "weekdays";
						let match = rrule.match(/BYHOUR=(\d+);BYMINUTE=(\d+)/);
						hour = +match[1];
						minute = +match[2];
						dateExStr = date.clone().set({ hour, minute }).locale("en").format("YYYYMMDDTHHmmss") + "Z";
						exDates = this.exDatesFromRRuleString(rrule);
					}

					if (repeats == "only") {
						if (!isValueAdded) {
							for (let interval of intervals) {
								let { start, end } = interval;

								let minutes = (end - start) * 60;

								let { hour, minute } = this.hourAndMinute(start);

								let rdate = "RDATE:" + date.clone().hour(hour).minute(minute).locale("en").format("YYYYMMDDTHHmmss") + "Z";

								let av = { duration: minutes, rrule: rdate };

								if (!isAvailableExist(av)) availability.push(av);
							}

							isValueAdded = true;
						}

						if (rruleType == "only") {
							let rruleDate = this.momentFromRdate(rrule);

							if (!rruleDate.isSame(date, "d")) {
								if (!isAvailableExist(available)) availability.push(available);
							}

							continue;
						}

						if (weekdays.includes(weekdayStr)) {
							if (rruleHasExclude) {
								if (!exDates.includes(dateExStr)) {
									rrule += "," + dateExStr;
								}
							} else {
								rrule += "\nEXDATE:" + dateExStr;
							}
						}

						let av = { duration, rrule };

						if (!isAvailableExist(av)) availability.push(av);
					} else if (repeats == "all") {
						if (!isValueAdded) {
							for (let interval of intervals) {
								let { start, end } = interval;

								let minutes = (end - start) * 60;

								let { hour, minute } = this.hourAndMinute(start);

								let rruleSet = new RRuleSet();

								rruleSet.rrule(
									new RRule({
										freq: RRule.WEEKLY,
										dtstart: this.invite.dateRange.start.clone().toDate(),
										until: this.invite.dateRange.end.clone().toDate(),
										byweekday: [date.clone().weekday()],
										byhour: [hour],
										byminute: [minute],
									})
								);

								let av = { duration: minutes, rrule: rruleSet.toString() };

								if (!isAvailableExist(av)) availability.push(av);
							}

							isValueAdded = true;
						}

						if (rruleType == "only") {
							if (this.momentFromRdate(rrule).weekday() != date.weekday()) {
								if (!isAvailableExist(available)) availability.push(available);
							}
						} else if (rruleType == "all") {
							if (weekdays[0] != weekdayStr) {
								if (rruleHasExclude) {
									if (exDates.includes(dateExStr)) {
										if (exDates.length == 1) {
											rrule = rrule.replace(/\nEXDATE:.*/, "");
										} else {
											let dates = exDates.filter((exDate) => exDate != dateExStr).join(",");

											rrule = rrule.replace(/\nEXDATE:.*/, "\nEXDATE:" + dates);
										}
									}
								}

								let av = { duration, rrule };

								if (!isAvailableExist(av)) availability.push(av);
							}
						} else if (rruleType == "weekdays") {
							if (rruleHasExclude) {
								if (exDates.includes(dateExStr)) {
									if (exDates.length == 1) {
										rrule = rrule.replace(/\nEXDATE:.*/, "");
									} else {
										let dates = exDates.filter((exDate) => exDate != dateExStr).join(",");

										rrule = rrule.replace(/\nEXDATE:.*/, "\nEXDATE:" + dates);
									}
								}
							}

							if (weekdays.includes(weekdayStr)) {
								let weekdayRegExp = new RegExp(`,${weekdayStr}|${weekdayStr},`, "g");

								rrule = rrule.replace(weekdayRegExp, "");
							}

							let av = { duration, rrule };

							if (!isAvailableExist(av)) availability.push(av);
						}
					} else if (repeats == "weekdays") {
						if (!isValueAdded) {
							for (let interval of intervals) {
								let { start, end } = interval;

								let minutes = (end - start) * 60;

								let { hour, minute } = this.hourAndMinute(start);

								let rruleSet = new RRuleSet();

								rruleSet.rrule(
									new RRule({
										freq: RRule.WEEKLY,
										dtstart: this.invite.dateRange.start.clone().toDate(),
										until: this.invite.dateRange.end.clone().toDate(),
										byweekday: [RRule.MO, RRule.TU, RRule.WE, RRule.TH, RRule.FR],
										byhour: [hour],
										byminute: [minute],
									})
								);

								let av = { duration: minutes, rrule: rruleSet.toString() };

								if (!isAvailableExist(av)) availability.push(av);
							}

							isValueAdded = true;
						}

						if (rruleType == "only") {
							let ruleWeekdayStr = this.momentFromRdate(rrule).locale("en").format("dd");

							if (["SA", "SU"].includes(ruleWeekdayStr)) {
								if (!isAvailableExist(available)) availability.push(available);
							}
						} else if (rruleType == "all") {
							if (["SA", "SU"].includes(weekdays[0])) {
								if (!isAvailableExist(available)) availability.push(available);
							}
						}
					}
				}

				this.invite.availability = availability;
			},

			weekdaysFromRRuleString(rrule) {
				if (rrule.indexOf("BYDAY") == -1) return [];

				let match = rrule.match(/BYDAY=([^;]+)/);

				return match[1].split(",");
			},

			momentFromRdate(rdate) {
				rdate = rdate.split(":")[1];

				return this.$moment(rdate).utc();
			},

			exDatesFromRRuleString(rrule) {
				if (rrule.indexOf("EXDATE") == -1) return [];

				let dates = rrule.split("EXDATE:")[1];

				dates = dates.split(",");

				return dates;
			},

			resetAvailability() {
				this.invite.availability = [];
			},

			preview() {
				this.$store.commit("setInvite", this.invite);

				this.$nextTick(() => this.$refs.preview.open());
			},

			next() {
				this.$v.$touch();

				if (this.$v.$invalid) return;

				this.$store.commit("setInvite", this.invite);

				this.$router.push("/create/group/options");
			},

			back() {
				this.$store.commit("setInvite", this.invite);

				this.$router.push("/create/group");
			},
		},
	};
</script>

