		.before_btn,
		.extend_btn,
		.after_btn {
			color: var(--primary);
			cursor: pointer;
			user-select: none;
			&.disabled {
				pointer-events: none;
				color: #b6b6b6;
				cursor: default;
			}
		}
	}
